ona. Suele rechazar los argumentos y validará las cosas con frecuencia buscando puntos débiles.")])]),_c('div',{staticClass:"tranform h-100 p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-8"},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/t2-51.svg"),"alt":"AvatarTop"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Cliente hablador")]),_c('p',{staticClass:"text-center"},[_vm._v("El cliente hablador, suele ser amistoso, sonriente, suele dar explicaciones no solicitadas, muestra gran seguridad, necesita atención y sentir que se le está prestando atención. En ocasiones, puede llegar a ser prepotente y llegar a hablar de temas que no conoce a profundidad sólo por el hecho de resaltar frente a otras personas.")])]),_c('div',{staticClass:"tranform h-100 p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-8"},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/t2-52.svg"),"alt":"AvatarTop"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Cliente indeciso")]),_c('p',{staticClass:"text-center"},[_vm._v("El cliente indeciso, suele dar excusas o evasivas para no tomar una decisión. Puede ser inseguro o tímido, le cuesta tomar decisiones por lo que es común que solicite aplazamientos antes de cerrar un tema en revisión. En ocasiones, acude a otros, para solicitar opiniones, tener referentes o puntos de vista.")])]),_c('div',{staticClass:"tranform h-100 p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-8"},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/t2-53.svg"),"alt":"AvatarTop"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Cliente grosero")]),_c('p',{staticClass:"text-center"},[_vm._v("El cliente grosero, suele sentir que tiene siempre la razón por lo que es bastante dominante, tiene mal carácter y mal humor, discute frecuentemente, tiende a ser ofensivo y/o agresivo. Puede llegar a ser irrespetuoso y desobligante. Suele d